.dark{
  &.wrapper{
    background-color: #171823;
  }
}

.hero {
  width: 375px;
  height: 198px;
  position: absolute;
  z-index: 1;

  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-image: url(../../images/bg-mobile-light.jpg);
}

.dark {
  .hero{
    background-image: url(../../images/bg-mobile-dark.jpg)};
}

.container {
  position: relative;
  z-index: 10;
  max-width: 327px;
  margin-left: auto;
  margin-right: auto;

  padding-bottom: 72px;
}

.header {
  display: flex;
  gap: 196px;
  color: white;
  align-items: center;
  padding-top: 48px;
}

.title {
  margin-top: 0;
  margin-bottom: 0;
  text-transform: uppercase;
  font-size: 20px;
  letter-spacing: 0.65em;
}

